import React, { useEffect } from "react";
import styles from "../App/App.module.scss";

// Import syntax highlighting if available
let Prism: any;
try {
  Prism = require("prismjs");
  // Import any language support you need
  require("prismjs/components/prism-javascript");
  require("prismjs/components/prism-typescript");
  require("prismjs/components/prism-jsx");
  require("prismjs/components/prism-tsx");
  require("prismjs/components/prism-json");
  require("prismjs/components/prism-css");
  require("prismjs/components/prism-scss");
  require("prismjs/components/prism-bash");
  require("prismjs/components/prism-markdown");
} catch (e) {
  console.warn("Prism syntax highlighting not available");
}

interface MarkdownRendererProps {
  content: string;
}

export const MarkdownRenderer: React.FC<MarkdownRendererProps> = ({
  content,
}) => {
  // Apply syntax highlighting after the component mounts
  useEffect(() => {
    if (Prism) {
      Prism.highlightAll();
    }
  }, [content]);

  const renderMarkdown = (markdown: string) => {
    // First, escape any HTML in the content
    let processedContent = escapeHtml(markdown);

    // Handle HTML entities that might be in the markdown
    processedContent = processedContent
      .replace(/&amp;#039;/g, "'")
      .replace(/&amp;quot;/g, '"')
      .replace(/&amp;lt;/g, "<")
      .replace(/&amp;gt;/g, ">")
      .replace(/&amp;amp;/g, "&");

    // Process code blocks with syntax highlighting
    processedContent = processedContent
      // Replace code blocks with placeholders and add syntax highlighting class
      .replace(/```(.*?)\n([\s\S]*?)```/g, (_, lang, code) => {
        const language = lang.trim() || "plaintext";
        const highlightedCode =
          Prism && Prism.languages[language]
            ? Prism.highlight(code.trim(), Prism.languages[language], language)
            : escapeHtml(code.trim());

        return `<pre class="code-block"><div class="code-header"><span class="language-badge">${escapeHtml(language)}</span></div><code class="language-${escapeHtml(language)}">${highlightedCode}</code></pre>`;
      })
      // Process inline code
      .replace(/`([^`]+)`/g, "<code>$1</code>")
      // Process headers
      .replace(/^# (.*?)$/gm, "<h1>$1</h1>")
      .replace(/^## (.*?)$/gm, "<h2>$1</h2>")
      .replace(/^### (.*?)$/gm, "<h3>$1</h3>")
      .replace(/^#### (.*?)$/gm, "<h4>$1</h4>")
      // Process bold and italic
      .replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>")
      .replace(/\*(.*?)\*/g, "<em>$1</em>")
      // Process links with special styling
      .replace(/\[(.*?)\]\((.*?)\)/g, (_, text, url) => {
        // Determine if it's an external link
        const isExternal =
          url.startsWith("http") && !url.includes(window.location.hostname);
        const externalMark = isExternal
          ? ' <span class="external-link-icon"></span>'
          : "";
        return `<a href="${escapeHtml(url)}" class="md-link" ${isExternal ? 'target="_blank" rel="noopener noreferrer"' : ""}>${text}${externalMark}</a>`;
      })
      // Process lists (unordered)
      .replace(/^\- (.*?)$/gm, "<li>$1</li>")
      // Process numbered lists
      .replace(/^\d+\. (.*?)$/gm, "<li>$1</li>");

    // Split into blocks and process paragraphs
    const blocks = processedContent.split(/\n\n+/);
    processedContent = blocks
      .map((block) => {
        // Skip if already processed as block element
        if (
          block.startsWith("<h") ||
          block.startsWith("<pre") ||
          block.startsWith("<ul") ||
          block.startsWith("<ol")
        ) {
          return block;
        }

        // Process lists
        if (block.includes("<li>")) {
          if (block.match(/^\d+\./m)) {
            return `<ol>${block}</ol>`;
          }
          return `<ul>${block}</ul>`;
        }

        // Process paragraphs
        return `<p>${block.replace(/\n/g, "<br />")}</p>`;
      })
      .join("");

    return { __html: processedContent };
  };

  const escapeHtml = (unsafe: string) => {
    return unsafe
      .replace(/&/g, "&amp;")
      .replace(/</g, "&lt;")
      .replace(/>/g, "&gt;")
      .replace(/"/g, "&quot;")
      .replace(/'/g, "&#039;");
  };

  return (
    <div
      className={styles.markdownContent}
      dangerouslySetInnerHTML={renderMarkdown(content)}
    />
  );
};

export default MarkdownRenderer;
